@import "base/mixins";
@import "base/button";
@import 'base/helpers';
@import 'base/forms';
@import 'base/typography';
// if the project is on wp
// @import 'base/wp-reset';

body {
	padding-top: 400px;
}

.module-links {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	background-color: #ccc;
	padding: 20px 10px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;

	z-index: 999999;
	a {
		font-size: 20px;
		padding: 5px 15px;
		margin: 5px;
		color: #000;
		background-color: tomato;
		&:hover {
			text-decoration: underline;
		}
	}
}
.module-block {
	border: 2px dashed tomato;
	margin-bottom: 20px;
}
.description-block {
	border: 2px dashed blue;
	padding: 15px;
	margin-bottom: 25px;
	pre {
		margin-bottom: 0;
	}
}